  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family:  "Rubik", sans-serif;
  font-weight: 300;
}

/* Skip to Content */
.skip-to-content {
  position: absolute;
  top: -40px;
  left: 0;
  background-color: #333;
  color: #fff;
  padding: 10px;
  z-index: 999;
  text-decoration: none;
}

.skip-to-content:focus {
  top: 0;
}

/* Navigation Bar Styles */
header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background-color:rgba(255,255,255,0.5);
  color: black;
  z-index: 1000;
  transition: background-color 0.3s ease;
}

.logo {
  font-size: 24px;
  font-weight: 700;
  color: black;
  text-decoration: none;
  margin-left: 5rem;
  display: inline-block;
  min-width: 44px;
  min-height: 44px;
}

nav ul {
  list-style-type: none;
  margin-right: 5rem;
}

nav ul li {
  display: inline-block;
  margin-right: 20px;
}

nav ul li a {
  text-decoration: none;
  color: black;
  font-weight: 500;
  transition: color 0.3s ease;
  padding: 10px; /* Added padding */
  display: inline-block; /* Added to ensure padding works */
  min-width: 44px;
  min-height: 44px;
}
nav ul li a:hover {
  color: white;
}

/* Hamburger Button Styles */
.hamburger {
  display: none;
  position: absolute;
  top: 10px; /* Adjusted top position */
  right: 20px;
  background: none;
  border: none;
  color: black;
  font-size: 24px;
  cursor: pointer;
  padding: 15px; /* Added padding */
}

#drawer-menu {
  display: none;
  position: fixed;
  top: 0;
  width: 250px;
  height: 100vh;
  transform: translate(-100%, 0);
  background-color:rgba(255,255,255,0.5);
  z-index: 2000;
  transition: transform 0.3s ease;
}
#drawer-menu ul {
  list-style-type: none;
  padding: 20px;
}
#drawer-menu ul li {
  margin-bottom: 15px;
}
#drawer-menu ul li a {
  text-decoration: none;
  color: black;
  font-weight: 500;
  transition: color 0.3s ease;
  padding: 15px; /* Added padding */
}
#drawer-menu ul li a:hover {
  color: white;
}

#drawer-menu.open {
  transform: translate(0); /* Menu ditampilkan */
}
/* Hero style */
.hero {
  position: relative;
  overflow: hidden;
  height: 80vh;
}

.hero img {
  width: 100%;
  height: auto;
  object-fit: cover;
  min-width: 1000px;
}

.hero-position {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
}

/* Restaurant Styles */
.list-title {
  margin-top: 20px;
  color: black;
  font-weight: 500;
  text-align: center;
  padding: 10px;
}

.card {
  display: flex;
  grid-template-columns: 3, 1fr;
  flex-direction: column;
  border: 1px solid #ccc;
  border-radius: 8px;
  overflow: hidden;
  margin-bottom: 20px;
  justify-content: center; 
  align-items: center; 
  text-align: center; 
}

.card-title a {
  padding: 15px;
  display: inline-block;
  min-width: 44px;
  min-height: 44px;
}

.resto-img {
  width: 50%;
  height: auto;
  justify-content: center;
  align-items: center;
}

.resto-title {
  padding: 10px;
  background-color: #f5f5f5;
}

.resto-title h3 {
  margin: 0;
}

.resto-title a {
  color: #333;
  text-decoration: none;
}

.resto-title a:hover {
  text-decoration: underline;
}

.resto-body {
  padding: 10px;
}

.resto-body p {
  margin: 0;
}
/* Detail */
.resto__name {
  margin: 5px;
  padding: 10px;
  font-weight: 500;
}

.resto__poster {
  width: 50%;
  margin-left: 25%;
  height: auto;
  justify-content: center;
  align-items: center;
}

.resto__info h3 {
  margin-top: 20px;
  color: black;
  font-weight: 500;
  text-align: center;
  padding: 10px;
}

.resto__info h4 {
  margin: 5px;
  padding: 10px;
  font-weight: 500;
}

.resto__info p {
  margin-left: 15px;
}

/*
  Like
*/
.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #db0000;
  
  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;
  
  font-size: 18px;
  color: white;
  cursor: pointer;
}


.button-detail {
  padding: 15px;
  display: inline-block;
  min-width: 44px;
  min-height: 44px;
}

/* Footer */
.footer {
  background-color: black;
  color: white;
  text-align: center;
  padding: 1rem 0;
}

.footer p {
  margin: 0;
}`, "",{"version":3,"sources":["webpack://./src/styles/main.css"],"names":[],"mappings":"AACA;EACE,SAAS;EACT,UAAU;EACV,sBAAsB;EACtB,iCAAiC;EACjC,gBAAgB;AAClB;;AAEA,oBAAoB;AACpB;EACE,kBAAkB;EAClB,UAAU;EACV,OAAO;EACP,sBAAsB;EACtB,WAAW;EACX,aAAa;EACb,YAAY;EACZ,qBAAqB;AACvB;;AAEA;EACE,MAAM;AACR;;AAEA,0BAA0B;AAC1B;EACE,kBAAkB;EAClB,MAAM;EACN,OAAO;EACP,WAAW;EACX,aAAa;EACb,8BAA8B;EAC9B,mBAAmB;EACnB,aAAa;EACb,sCAAsC;EACtC,YAAY;EACZ,aAAa;EACb,sCAAsC;AACxC;;AAEA;EACE,eAAe;EACf,gBAAgB;EAChB,YAAY;EACZ,qBAAqB;EACrB,iBAAiB;EACjB,qBAAqB;EACrB,eAAe;EACf,gBAAgB;AAClB;;AAEA;EACE,qBAAqB;EACrB,kBAAkB;AACpB;;AAEA;EACE,qBAAqB;EACrB,kBAAkB;AACpB;;AAEA;EACE,qBAAqB;EACrB,YAAY;EACZ,gBAAgB;EAChB,2BAA2B;EAC3B,aAAa,EAAE,kBAAkB;EACjC,qBAAqB,EAAE,kCAAkC;EACzD,eAAe;EACf,gBAAgB;AAClB;AACA;EACE,YAAY;AACd;;AAEA,4BAA4B;AAC5B;EACE,aAAa;EACb,kBAAkB;EAClB,SAAS,EAAE,0BAA0B;EACrC,WAAW;EACX,gBAAgB;EAChB,YAAY;EACZ,YAAY;EACZ,eAAe;EACf,eAAe;EACf,aAAa,EAAE,kBAAkB;AACnC;;AAEA;EACE,aAAa;EACb,eAAe;EACf,MAAM;EACN,YAAY;EACZ,aAAa;EACb,8BAA8B;EAC9B,sCAAsC;EACtC,aAAa;EACb,+BAA+B;AACjC;AACA;EACE,qBAAqB;EACrB,aAAa;AACf;AACA;EACE,mBAAmB;AACrB;AACA;EACE,qBAAqB;EACrB,YAAY;EACZ,gBAAgB;EAChB,2BAA2B;EAC3B,aAAa,EAAE,kBAAkB;AACnC;AACA;EACE,YAAY;AACd;;AAEA;EACE,uBAAuB,EAAE,qBAAqB;AAChD;AACA,eAAe;AACf;EACE,kBAAkB;EAClB,gBAAgB;EAChB,YAAY;AACd;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,iBAAiB;EACjB,iBAAiB;AACnB;;AAEA;EACE,kBAAkB;EAClB,MAAM;EACN,OAAO;EACP,WAAW;EACX,YAAY;EACZ,oCAAoC;EACpC,UAAU;AACZ;;AAEA,sBAAsB;AACtB;EACE,gBAAgB;EAChB,YAAY;EACZ,gBAAgB;EAChB,kBAAkB;EAClB,aAAa;AACf;;AAEA;EACE,aAAa;EACb,6BAA6B;EAC7B,sBAAsB;EACtB,sBAAsB;EACtB,kBAAkB;EAClB,gBAAgB;EAChB,mBAAmB;EACnB,uBAAuB;EACvB,mBAAmB;EACnB,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,qBAAqB;EACrB,eAAe;EACf,gBAAgB;AAClB;;AAEA;EACE,UAAU;EACV,YAAY;EACZ,uBAAuB;EACvB,mBAAmB;AACrB;;AAEA;EACE,aAAa;EACb,yBAAyB;AAC3B;;AAEA;EACE,SAAS;AACX;;AAEA;EACE,WAAW;EACX,qBAAqB;AACvB;;AAEA;EACE,0BAA0B;AAC5B;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,SAAS;AACX;AACA,WAAW;AACX;EACE,WAAW;EACX,aAAa;EACb,gBAAgB;AAClB;;AAEA;EACE,UAAU