@media screen and (min-width: 1200px) {
    .hero img {
      min-width: 1000px;
    }
  }
  @media screen and (max-width: 768px) {
    header {
      flex-direction: column;
      align-items: flex-start;
      background-color:rgba(255,255,255,0);
    }
    nav {
      display: none;
    }
    #drawer-menu {
      display: block;
    }
    .hamburger {
      display: block;
      color: white;
    }
  
    .logo {
      margin-bottom: 10px;
      margin-left: 5px;
      color: white;
    }
  
    .restaurant {
      width: 100%;
    }

    .restaurant-container {
      grid-template-columns: repeat(auto-fit, minmax(200px, 1fr)); /* Menyesuaikan untuk layar seluler yang lebih kecil */
    }
  
